//ios android 通信
  var u = navigator.userAgent;
  var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
  var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 


global.iosORandroid = {
   isAndroid:isAndroid,
   isiOS:isiOS,
   getAccessToken:function(token){
     localStorage.setItem("accessToken",token);   
   },
  urlRedirect:function(transition,urls,permissions){
      switch(urls){
        case '/cb'://成本管理
        if(permissions=='1'){
           transition.next();
        }else if(permissions=='202'||permissions=='203'||permissions=='201') {//ADD,SMD,FPI
           transition.redirect('/cbsa');
        }
        break;
         case '/ll'://品质管
        if(permissions=='1'){
           transition.next();
        }else if(permissions=='202'){//ADD
           transition.redirect('/lladd');
        }
        else if(permissions=='203'||permissions=='201'){//SMD,FPI
           transition.redirect('/llsmd');
        }
        break;
         case '/xl'://生产制造
        if(permissions=='1'){
           transition.next();
        }else if(permissions=='202'){//ADD
           transition.redirect('/xladd');
        }
        else if(permissions=='203'||permissions=='201'){//SMD,FPI
           transition.redirect('/xlsmd');
        }
        break;
        case '/lr'://利润
        if(permissions=='1'){
          transition.next();
        }else if(permissions=='202'||permissions=='203'||permissions=='201') {//ADD,SMD,FPI
           transition.redirect('/profitSA');
        }
        break;
        case '/salesmanager'://销售管理
        if(permissions=='1'){
          transition.next();
        }else if(permissions=='202'||permissions=='203'||permissions=='201') {//ADD,SMD,FPI
           transition.redirect('/salesSA');
        }
        break;

        default:
        transition.next();
        break;
     }
    
  }
}


