var Vue = require('vue');
let echart = require('echarts');
Vue.directive('ebar',{
    deep: true,
    params: ['loading'],
    paramWatchers: {
        loading: function (val, oldVal) {
            var _this = this;

            if (val === true) {
                _this.instance.showLoading();
            } else {
                _this.instance.hideLoading();
            }
        }
    },
    bind: function () {
        var _this = this;
        Vue.nextTick(function () {

            // init echarts instance
            _this.instance = echart.init(_this.el);
            _this.instance.on('click', function (param) {
                if (typeof param.seriesIndex == 'undefined') {
                    return;
                }
                if (param.type == 'click') {
                    var backClick = { id: _this.el.id, xValue: param.name };
                    _this.vm.$emit('eChartClick', backClick);
                }
            });
            // show loading animation
            if (_this.params.loading === true) {
                _this.instance.showLoading();
            }

            _this.resizeEventHandler = function () {
                _this.instance.resize();
            };

            _this.el.addEventListener('resize', _this.resizeEventHandler, false);

            if (window.attachEvent) {
                window.attachEvent('onresize', _this.resizeEventHandler);
            } else {
                window.addEventListener('resize', _this.resizeEventHandler, false);
            }
        });
    },
    update: function (val, oldVal) {
        var _this = this;
        var option = {
            backgroundColor: '#2d3e50',
            color: global.formatData.colors,
            title: {
                x: 'center',
                padding: 20,
                textStyle: {
                    color: '#fff'
                }
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                x: 'center',
                y: 'bottom',
                data: [],
                textStyle: {
                    color: '#fff'
                }
            },
            calculable: true,
            grid:{},
            xAxis: [
                {
                    type: 'category',
                    data: [],
                    axisLine : {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: '#bebebe',
                            width: 2,
                            type: 'solid'
                        }
                    },
                    axisLabel : {           // 坐标轴文本标签，详见axis.axisLabel
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#909fa0'
                        }
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: '#bebebe',
                            width: 2,
                            type: 'solid'
                        }
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#909fa0'
                        }
                    }
                }
            ],
            series: []
        };
        var i = 0;
        if (val.color != null) {
            option.color = val.color;
        }
        val.dataset.forEach(function (item) {
            option.legend.data.push(item.label);
            var seriesInit = {
                type: 'bar',
                // markPoint: {
                //     data: [
                //         { type: 'max', name: '最大值' },
                //         { type: 'min', name: '最小值' }
                //     ]
                // },
                markLine: {
                    data: [
                        { type: 'average', name: '平均值' }
                    ]
                },
                data: []
            };
            if (item.workDescribe != null && i == 0) {
                option.title.text = item.workDescribe;
                option.savetitle = item.workDescribe;
            }
            seriesInit.name = item.label;
            seriesInit.id = item.label;
            option.series.push(seriesInit);
            var optionData = item.data;
            if (typeof (optionData) == "object") {
                optionData.forEach(function (j) {
                    if (i == 0) {
                        option.xAxis[0].data.push(j[0]);
                    }
                    option.series[i].data.push(j[1]);
                });
            }
            else {
                if (i == 0) {
                    option.xAxis.data.push(item.label);
                }
            }
            i++;
        });
        if (option.legend.data.length > 4) {
            //legend太多的情况
            option.grid.y2 = 100;
        }
        else {
            option.grid.y2 = 50;
        }


        Vue.nextTick(function () {
            _this.instance.clear(); // echarts Redraw
            if (option.legend.data.length == 0) {
                option.title.text = "暂无数据";
            }
            else {
                option.title.text = option.savetitle;
            }
            //if (option.legend.data.length == 0) {
            //    _this.instance.showLoading({
            //        text: '当前无数据',
            //        textColor:  '#fff'
            //    });
            //    return [];
            //}
            //else {
            //    _this.instance.hideLoading();
            //}
            _this.instance.setOption(option);
        });
    },
    unbind: function () {
        var _this = this;

        _this.instance.dispose();

        if (window.attachEvent) {
            window.detachEvent('onresize', _this.resizeEventHandler);
        } else {
            window.removeEventListener('resize', _this.resizeEventHandler, false);;
        }
    }
});

Vue.directive('ebarline', {
    deep: true,
    params: ['loading'],
    paramWatchers: {
        loading: function (val, oldVal) {
            var _this = this;

            if (val === true) {
                _this.instance.showLoading();
            } else {
                _this.instance.hideLoading();
            }
        }
    },
    bind: function () {
        var _this = this;
        Vue.nextTick(function () {
            // init echarts instance
            _this.instance = echart.init(_this.el);
            _this.instance.on('click', function (param) {
                if (typeof param.seriesIndex == 'undefined') {
                    return;
                }
                if (param.type == 'click') {
                    var backClick = { id: _this.el.id, xValue: param.name };
                    _this.vm.$emit('eChartClick', backClick);
                }
            });
            // show loading animation
            if (_this.params.loading === true) {
                _this.instance.showLoading();
            }
            _this.resizeEventHandler = function () {
                _this.instance.resize();
            };
            _this.el.addEventListener('resize', _this.resizeEventHandler, false);
            if (window.attachEvent) {
                window.attachEvent('onresize', _this.resizeEventHandler);
            } else {
                window.addEventListener('resize', _this.resizeEventHandler, false);
            }
        });
    },
    update: function (val, oldVal) {
        var _this = this;
        //if (val.dataset.length == 0) {
        //    return null;
        //}
        var option = {
            backgroundColor: '#2d3e50',
            //color: global.formatData.colors,
            title: {
                x: 'center',
                padding: 20,
                textStyle: {
                    color: '#fff'
                }
            },
            tooltip: {
                trigger: 'yAxis'
            },
            legend: {
                x: 'center',
                y: 'bottom',
                data: [],
                textStyle: {
                    color: '#fff'
                }
            },
            xAxis: [
                {
                    type: 'category',
                    data: [],
                    nameTextStyle: { color: '#909fa0' },
                    axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: '#bebebe',
                            width: 2,
                            type: 'solid'
                        }
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#909fa0'
                        }
                    }
                }
            ],
            grid:{
            },
            yAxis: [
                {
                    type: 'value',
                    axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: '#bebebe',
                            width: 2,
                            type: 'solid'
                        }
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#909fa0'
                        }
                    }
                }
            ],
            series: []
        };
        var temp = "";
        var yAxis = [];
        var yAxisLegendList = [];
        var i = 0;
        if (val.color != null) {
          option.color = val.color;
        }
        val.dataset.forEach(function (item) {
            if (item.title != null) {
                if (item.title != temp) {
                    yAxis.push(item.title);
                    temp = item.title;
                }
            }
        });
        yAxis.forEach(function (item) {
            var yAxisLegend = {};
            yAxisLegend.name = item;
            yAxisLegend.type = "value";
            var axisLine = {            // 坐标轴线
                lineStyle: {       // 属性lineStyle控制线条样式
                    color: '#bebebe',
                    width: 2,
                    type: 'solid'
                }
            };
            var axisLabel = {           // 坐标轴文本标签，详见axis.axisLabel
                textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                    color: '#909fa0'
                }
            };
            yAxisLegend.axisLine = axisLine;
            yAxisLegend.axisLabel = axisLabel;
            yAxisLegendList.push(yAxisLegend);
        });
        if (yAxisLegendList.length > 0) {
            option.yAxis = yAxisLegendList;
        }
        var i = 0;

        val.dataset.forEach(function (item) {
            option.legend.data.push(item.label);
            var seriesInit = {
                type: 'bar',
                data: [],
                nameTextStyle: {},
                color:[]
            };

            if (item.workDescribe != null && i == 0) {
                option.title.text = item.workDescribe;
                option.savetitle = item.workDescribe;
            }
            seriesInit.name = item.label;
            seriesInit.id = item.label;
            //seriesInit.nameTextStyle.color = '#fff';
            // if(val.color != null){
            //   seriesInit.color= val.color
            // }
            if (yAxis.length > 1) {
                if (yAxis[0] == item.title) {
                    seriesInit.type = "bar";
                    seriesInit.yAxisIndex = 0;
                    seriesInit.color=[global.formatData.colorBars[i]];
                  if(val.color != undefined){
                    seriesInit.color= val.color
                  }
                }
                else {
                    seriesInit.type = "line";
                    seriesInit.yAxisIndex = 1;
                    seriesInit.color=[global.formatData.colorsLine[i]];
                }
            }

            option.series.push(seriesInit);
            var optionData = item.data;
            if (typeof (optionData) == "object") {
                optionData.forEach(function (j) {
                    if (i == 0) {
                        var axisLine = {            // 坐标轴线
                            lineStyle: {       // 属性lineStyle控制线条样式
                                color: '#bebebe',
                                width: 2,
                                type: 'solid'
                            }
                        };
                        var axisLabel = {           // 坐标轴文本标签，详见axis.axisLabel
                            textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                                color: '#909fa0'
                            }
                        };
                        option.xAxis[0].axisLine = axisLine;
                        option.xAxis[0].axisLabel = axisLabel;
                        option.xAxis[0].data.push(j[0]);
                    }
                    option.series[i].data.push(j[1]);
                });
            }
            else {
                if (i == 0) {
                    var axisLabel = {           // 坐标轴文本标签，详见axis.axisLabel
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff'
                        }
                    };
                    var axisLine = {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: '#fff',
                            width: 2,
                            type: 'solid'
                        }
                    };
                    option.xAxis.axisLine = axisLine;
                    option.xAxis.axisLabel = axisLabel;
                    option.xAxis.data.push(item.label);
                }
            }
            i++;
        });

        if (option.legend.data.length > 4) {
            //legend太多的情况
            option.grid.y2 = 100;
        }
        else {
            option.grid.y2 = 50;
        }

        Vue.nextTick(function () {
            _this.instance.clear(); // echarts Redraw
            if (option.legend.data.length == 0) {
                //option.xAxis = [];
                option.title.text = "暂无数据";
            }
            else {
                option.title.text = option.savetitle;
            }
            //if (option.legend.data.length == 0) {
            //    _this.instance.showLoading({
            //        text: '当前无数据',
            //        textColor: '#fff'
            //    });
            //    return [];
            //}
            //else {
            //    _this.instance.hideLoading();
            //}
            _this.instance.setOption(option);
        });
    },
    unbind: function () {
        var _this = this;

        _this.instance.dispose();

        if (window.attachEvent) {
            window.detachEvent('onresize', _this.resizeEventHandler);
        } else {
            window.removeEventListener('resize', _this.resizeEventHandler, false);;
        }
    }
});

Vue.directive('epie', {
    deep: true,
    params: ['loading'],
    paramWatchers: {
        loading: function (val, oldVal) {
            var _this = this;

            if (val === true) {
                _this.instance.showLoading();
            } else {
                _this.instance.hideLoading();
            }
        }
    },
    bind: function () {
        var _this = this;

        Vue.nextTick(function () {
            // init echarts instance
            _this.instance = echart.init(_this.el);
            _this.instance.on('click', function (param) {
                if (typeof param.seriesIndex == 'undefined') {
                    return;
                }
                if (param.type == 'click') {
                    var backClick = { id: _this.el.id, xValue: param.name };
                    _this.vm.$emit('eChartClick', backClick);
                }
            });
            // show loading animation
            if (_this.params.loading === true) {
                _this.instance.showLoading();
            }

            _this.resizeEventHandler = function () {
                _this.instance.resize();
            };

            _this.el.addEventListener('resize', _this.resizeEventHandler, false);

            if (window.attachEvent) {
                window.attachEvent('onresize', _this.resizeEventHandler);
            } else {
                window.addEventListener('resize', _this.resizeEventHandler, false);
            }
        });
    },
    update: function (val, oldVal) {
        var _this = this;
        //if (val.dataset.length == 0) {
        //    return null;
        //}
        console.log(val);
        var option = {
            backgroundColor: '#2d3e50',
            color: global.formatData.colors,
            title: {
                x: 'center',
                padding: 20,
                textStyle: {
                    color: '#fff'
                }
            },
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            calculable: true,
            grid: {},
            legend: {
                x: 'center',
                y: 'bottom',
                data: [],
                textStyle: {
                    color: '#fff'
                }
            },
            series: [
                {
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '50%'],
                    data: []
                }
            ]
        };
        if (val.dataset.length > 1) {
            val.dataset.forEach(val.dataset, function (item) {
                var dataset = item;
                if (item.workDescribe != null && i == 0) {
                    option.title.text = item.workDescribe;
                    option.savetitle = item.workDescribe;
                }
                var seriesData = {};
                var data = 0;
                seriesData.name = dataset.label;
                if (dataset.data != null) {
                    $.each(dataset.data, function (k) {
                        if (k.length > 1) {
                            data += parseInt(k[1]);
                        }
                        else {
                            data += parseInt(k);
                        }
                    });
                }
                seriesData.value = data;
                option.series[0].data.push(seriesData);
                option.legend.data.push(seriesData.name);
            });
        }
        else if (val.dataset.length == 1) {
            var dataset = val.dataset[0];
            if (dataset.workDescribe != null) {
                option.title.text = dataset.workDescribe;
                option.savetitle = dataset.workDescribe;
            }
            var data = 0;
            dataset.data.forEach(function (k) {
                var seriesData = {};
                if (k.length > 1) {
                    data = parseInt(k[1]);
                    seriesData.name = k[0];
                    seriesData.value = data;
                }
                else {
                    data = parseInt(k);
                    seriesData.name = dataset.label;
                    seriesData.value = data;
                }
                seriesData.label = {};
                seriesData.label.normal = {};
                seriesData.label.normal.formatter = "{b}\n({d}%)";
                option.series[0].data.push(seriesData);
                option.legend.data.push(seriesData.name);
            });
        }

        if (option.legend.data.length > 4) {
            //legend太多的情况
            option.grid.y2 = 100;
        }
        else {
            option.grid.y2 = 50;
        }

        Vue.nextTick(function () {
            _this.instance.clear(); // echarts Redraw
            //if (option.legend.data.length == 0) {
            //    _this.instance.showLoading({
            //        text: '当前无数据',
            //        textColor: '#fff'
            //    });
            //    return [];
            //}
            //else {
            //    _this.instance.hideLoading();
            //}
            if (option.legend.data.length == 0) {
                option.title.text = "暂无数据";
            }
            else {
                option.title.text = option.savetitle;
            }
            _this.instance.setOption(option);
        });
    },
    unbind: function () {
        var _this = this;

        _this.instance.dispose();

        if (window.attachEvent) {
            window.detachEvent('onresize', _this.resizeEventHandler);
        } else {
            window.removeEventListener('resize', _this.resizeEventHandler, false);;
        }
    }
});

