Vue.directive('egauge', {
  deep: true,
  params: ['loading'],
  paramWatchers: {
    loading: function(val, oldVal) {
      var _this = this;

      if (val === true) {
        _this.instance.showLoading();
      } else {
        _this.instance.hideLoading();
      }
    }
  },
  bind: function() {
    var _this = this;
    Vue.nextTick(function() {

      // init echarts instance
      _this.instance = echart.init(_this.el);
      _this.instance.on('click', function(param) {
        if (typeof param.seriesIndex == 'undefined') {
          return;
        }
        if (param.type == 'click') {
          var backClick = { id: _this.el.id, xValue: param.name };
          _this.vm.$emit('eChartClick', backClick);
        }
      });
      // show loading animation
      if (_this.params.loading === true) {
        _this.instance.showLoading();
      }

      _this.resizeEventHandler = function() {
        _this.instance.resize();
      };

      _this.el.addEventListener('resize', _this.resizeEventHandler, false);

      if (window.attachEvent) {
        window.attachEvent('onresize', _this.resizeEventHandler);
      } else {
        window.addEventListener('resize', _this.resizeEventHandler, false);
      }
    });
  },
  update: function (val, oldVal) {
    var _this = this;

    var option = {
        backgroundColor: '#2d3e50',
        color: global.formatData.colors,
        title: {
            x: 'center',
            bottom:'30%',
        },
        tooltip: {
            formatter: "{a} <br/>{b} : {c}%"
        },
        series: []
    };
    if (val.dataset.length == 1) {
        var seriesData = {
            name: '',
            type: 'gauge',
            min: 0,
            detail: { formatter: '{value}' },
            data: [],
            itemStyle: {
                normal: { color: '#fff' }
            }
        };
        var dataset = val.dataset[0];
        if (val.dataset[0].title != null) {
            option.title.text = val.dataset[0].title;
            option.savetitle = val.dataset[0].title;
        }
        var dataChild = {};
        var data = 0;
        if (typeof (dataset.data) == 'object') {
            dataset.data.forEach(function (itemEach) {
                if (itemEach.length > 1) {
                    data += parseInt(itemEach[1]);
                }
                else {
                    data += parseInt(itemEach);
                }
            });
        }
        else {
            data = dataset.data;
        }
        dataChild.value = data;
        dataChild.name = val.dataset[0].label;
        seriesData.max = data;
        seriesData.data.push(dataChild);
        option.series.push(seriesData);
    }
    else if (val.dataset.length == 2) {
        var seriesData = {
            name: '',
            type: 'gauge',
            startAngle:'180',
            endAngle:'0',
            min:0,
            max:100,
            axisLine:{
              "lineStyle": {
                "width": 25, //柱子的宽度
                "color": [[0.2, "#02c1f8"],[0.4, "#76b0db"],[0.6, "#7a95b2"],[0.8, "#ba7178"],[1, "#ff6068"]]  //0.298是百分比的比例值（小数），还有对应两个颜色值
              }
            },
            pointer: {
              width: "10%",
              color: "black"
            },
            itemStyle: {
              normal: {
                color: "rgba(255, 255, 255, 0.8)",
                shadowBlur: 10
              }
            },
            axisTick: {
              "show": false
            },
            axisLabel: {
              "show": false
            },
            splitLine: {
              "show": false
            },
            //radius:'300',
            //min: 0,
            //max: 100,
            detail: {
              formatter: '{value}%',
              show: !0,
              //backgroundColor: "rgba(0,0,0,0)",
              borderWidth: 0,
              borderColor: "#ffffff",
              offsetCenter: [0, 30],
              textStyle: {
                fontSize:15,
                color:'#ffffff',
                fontWeight:'bold'
              }
            },

            data: []
        };
        var dataChild = 0;
        var dataSecondChild = 0;
        var data = 0;
        var i = 0;
        val.dataset.forEach(function (item) {
            var dataset = item;
            if (dataset.workDescribe != null) {
                option.title.text = dataset.workDescribe;
                option.savetitle = dataset.workDescribe;
                option.title.textStyle={
                  color:'#ffffff',
                  //fontWeight:'normal',
                  fontSize:20
                };
                option.title.bottom =25
            }
            if (typeof (dataset.data) == 'object') {
                dataset.data.forEach( function (itemEach) {
                    if (itemEach.length > 1) {
                        data += parseInt(itemEach[1]);
                    }
                    else {
                        data += parseInt(itemEach);
                    }
                });
            }
            else {
                data = dataset.data;
            }
            if (i == 0) {
                dataChild = data;
            }
            else if (i == 1) {
                dataSecondChild = data;
            }
            i++;
        });
        data = (dataChild / dataSecondChild * 100).toFixed(2);
        seriesData.data.push(data);
        option.series.push(seriesData);
    }

    Vue.nextTick(function() {
        _this.instance.clear(); // echarts Redraw
        //if (option.series.length == 0) {
        //    _this.instance.showLoading({
        //        text: '当前无数据',
        //        textColor: '#fff'
        //    });
        //    return [];
        //}
        //else {
        //    _this.instance.hideLoading();
        //}
        if (option.series.length == 0) {
            option.title.text = "暂无数据";
        }
        else {
            option.title.text = option.savetitle;
        }
      _this.instance.setOption(option);
    });
  },
  unbind: function() {
    var _this = this;

    _this.instance.dispose();

    if (window.attachEvent) {
      window.detachEvent('onresize', _this.resizeEventHandler);
    } else {
      window.removeEventListener('resize', _this.resizeEventHandler, false);;
    }
  }
});



