//=======================
let year = []
let dataY=new Date();

let NowYear=dataY.getFullYear();
let defaultTime=[NowYear+'年','全年'];
let defaultTimeYear=[NowYear+'年'];
let localStorageyear= localStorage.getItem('year',);
let localStoragemon= localStorage.getItem('mon');
if(localStorageyear&&localStorageyear!=''){
    if(localStoragemon&&localStoragemon!=''){
    defaultTime=[localStorageyear,localStoragemon];
    }else{
         defaultTime=[localStorageyear,'全年'];
    }

    defaultTimeYear=[localStorageyear];
}

for (var i = NowYear-10; i <= NowYear; i++) {
  year.push({
    name: i + '年',
    value: i + '年'
  })
}
let yearData=[year]

let months = []
for (var i = 0; i <= 12; i++) {
  if(i==0){
    months[0]={
      name: '全年',
      value: '全年'
    }
  }else{
    months.push({
      name: i + '月',
      value: i + '月'
    })
  }
}
let TimeData=[year,months]

global.formatData = {
    PrefixInteger:function(num, length) {
     return (Array(length).join('0') + num).slice(-length);
    },
   //年份转换
   dateConversion:function(item){
   var year=item[0].split('年')[0];
   var mon=item[1];
   var date='';

  if(mon){
   if(mon=='全年'){
     date=year
   }else{
      mon=  mon.split('月')[0];
      mon= formatData.PrefixInteger(mon,2);
      date=year+mon;
   }
  }else{
       date=year
  }
   return date;
   },
    setDateTime:function (item) {
      var year=item[0];
      var mon=item[1];
      var date='';
       
       if(mon){
          formatData.defaultTime=[year,mon];
          localStorage.setItem('mon',mon);
       }
    
      formatData.defaultTimeYear=[year];
      localStorage.setItem('year',year); 

    },

    apiBaseUrl:'http://192.168.31.27:8099/bi-web',//'',///bi-web，
    defaultTime:defaultTime,
    defaultTimeYear:defaultTimeYear,
    //事业部
    busList:[{name: 'FPI', value: 'FPI'},{name: 'SMD', value: 'SMD'},{name: 'ADD', value: 'ADD'}],
    //成本
    costList:[{name: '物料', value: '物料'},{name: '人工', value: '人工'},{name: '水电', value: '水电'},{name: '项目', value: '项目'},{name: '差旅', value: '差旅'},{name: '折旧', value: '折旧'}],
    //时间
    dateList:TimeData,
    dateListYear: yearData,


    timeChangeData: function (data, oneValue) {
        if (data.latitudeLi.length > 0) {
            if (data.latitudeLi[0].columnNameEn == "DATE_") {
                if (data.latitudeTime == "MONTH") {
                    if (typeof (oneValue) == "number") {
                        var month = parseInt(oneValue.toString().substring(4)).toString() + "月";
                        return month;
                    }
                }
                else if (data.latitudeTime == "YEAR") {
                    if (typeof (oneValue) == "number") {
                        var month = oneValue.toString() + "年";
                        return month;
                    }
                }
            }
        }
        return oneValue;
    },

    //图表使用的颜色数组
    colors: ['#1e73bd','#40c86c','#ef84b1','#527bef', '#fab840', '#bb66f2'],
    colorBars: ['#30aeef','#f25a29', '#fab840', '#8ec63f', '#ef84b1'],
    colorsLine: ['#efef00','#efef00','#fab840','#efef00','#44edb9','#fab840'],
    /*
        功能说明：接收服务接口后整理成给普通报表的数据格式
        参数说明：
        输入：服务端返回的数据
        输出：符合普通报表显示的数据
    */
    formatGerTableData: function (data) {
        var i = 0;
        var gerData = new Object();
        var headlist = [];
        var gerHeader = new Object();
        var datalist = [];
        var arr = {
            headList: [],
            dataList: []
        }

        var wd = data.latitudeLi[0].columnNameEn;
        gerHeader.name = data.latitudeLi[0].columnNameZh;   //将维度放在第一列
        gerHeader.field = data.latitudeLi[0].columnNameEn;
        gerHeader.minWidth = "130";
        headlist.push(gerHeader);

        for (i = 0; i < data.indexLi.length; i++) {
            gerHeader = new Object();
            gerHeader.name = data.indexLi[i].columnNameZh;
            gerHeader.field = data.indexLi[i].columnNameEn;
            gerHeader.minWidth = "130";
            headlist.push(gerHeader);
            //headerlistEn.push(gerHeader["name"]);
        }

        for (i = 0; i < data.result.list.length; i++) {
            gerData = new Object();
            for (var key in data.result.list[i]) {
                headlist.forEach(function (idx, obj) {
                    if (key == obj.field) {
                        gerData[key] = data.result.list[i][key];
                    }
                });
            }
            datalist.push(gerData);
        }
        arr.headList = headlist;
        arr.dataList = datalist;
        return arr;
    },
    MultiIndexOneLat: function (data) {
        var arrs = [];
        var showData = {
            title: "",
            labelId: "",
            label: "",
            color: "",
            workDescribe: "",
            data: []
        }
        var oneLatitude = [];
        var one = data.latitudeLi[0].columnNameEn;
        for (var k = 0; k < data.indexLi.length; k++) {
            showData = {
                title: data.indexLi[k].columnNameZh,
                labelId: data.indexLi[k].columnNameEn,
                label: data.indexLi[k].columnNameZh,
                color: formatData.colors[k % 10],
                workDescribe: data.workDescribe,
                data: []
            }
            for (var j = 0; j < data.result.list.length; j++) {
                var arr = [];
                var oneValue = data.result.list[j][data.latitudeLi[0].columnNameEn];
                //特殊处理
                if (data.latitudeLi[0].columnNameEn == "REGION_") {
                    arr.push(data.result.list[j]["REGION_NAME"]);
                }
                else {
                    oneValue = formatData.timeChangeData(data, oneValue);
                    arr.push(oneValue);
                }
                arr.push(data.result.list[j][data.indexLi[k].columnNameEn]);
                showData.data.push(arr);
            }
            arrs.push(showData);
        }
        return arrs;
    },

    OneIndexTwoLat: function (data) {
        var arrs = [];
        var showData = {
            title: "",
            labelId: "",
            label: "",
            color: "",
            workDescribe: "",
            data: []
        }
        var secondLatitude = [];
        var secondLatitudeRealName = [];
        var oneLatitude = [];
        var one = data.latitudeLi[0].columnNameEn;
        var second = data.latitudeLi[1].columnNameEn;
        for (var k = 0; k < data.latitudeLi.length; k++) {
            for (var i = 0; i < data.result.list.length; i++) {
                var flag = false;
                for (var oneL = 0; oneL < secondLatitude.length; oneL++) {
                    if (secondLatitude[oneL] == data.result.list[i][second]) {
                        flag = true;
                        break;
                    }
                }
                if (!flag) {
                    secondLatitude.push(data.result.list[i][second]);
                    if (second == 'REGION_') {
                        secondLatitudeRealName.push(data.result.list[i]["REGION_NAME"]);
                    }
                }
                flag = false;
                for (var oneL = 0; oneL < oneLatitude.length; oneL++) {
                    if (oneLatitude[oneL] == data.result.list[i][one]) {
                        flag = true;
                        break;
                    }
                }
                if (!flag) {
                    oneLatitude.push(data.result.list[i][one]);
                }
            }
        }
        for (var k = 0; k < secondLatitude.length; k++) {
            var arr = [];
            var secondValue = secondLatitude[k];
            var secondRealValue = secondLatitudeRealName[k];
            if (second != 'REGION_') {
                secondRealValue = secondValue;
            }
            showData = {
                title: data.indexLi[0].columnNameZh,
                labelId: secondValue,
                label: secondRealValue,
                color: formatData.colors[k % 10],
                workDescribe: data.workDescribe,
                data: []
            }

            //特殊处理

            for (var i = 0; i < oneLatitude.length; i++) {
                var oneValue = oneLatitude[i];
                for (var j = 0; j < data.result.list.length; j++) {
                    if (data.result.list[j][data.latitudeLi[0].columnNameEn] == oneValue) {
                        if (data.result.list[j][data.latitudeLi[1].columnNameEn] == secondValue) {
                            arr = [];
                            oneValue = formatData.timeChangeData(data, oneValue);
                            arr.push(oneValue);
                            arr.push(data.result.list[j][data.indexLi[0].columnNameEn]);
                            showData.data.push(arr);
                            break;
                        }
                    }
                }
            }
            arrs.push(showData);
        }
        return arrs;
    },
    TwoIndexTwoLat: function (data) {
        var arrs = [];
        var showData = {
            title: "",
            labelId: "",
            label: "",
            color: "",
            workDescribe: "",
            data: []
        }
        var secondLatitude = [];
        var secondLatitudeRealName = [];
        var oneLatitude = [];
        var one = data.latitudeLi[0].columnNameEn;
        var second = data.latitudeLi[1].columnNameEn;
        for (var k = 0; k < data.latitudeLi.length; k++) {
            for (var i = 0; i < data.result.list.length; i++) {
                var flag = false;
                for (var oneL = 0; oneL < secondLatitude.length; oneL++) {
                    if (secondLatitude[oneL] == data.result.list[i][second]) {
                        flag = true;
                        break;
                    }
                }
                if (!flag) {
                    secondLatitude.push(data.result.list[i][second]);
                    if (second == 'REGION_') {
                        secondLatitudeRealName.push(data.result.list[i]["REGION_NAME"]);
                    }
                }
                flag = false;
                for (var oneL = 0; oneL < oneLatitude.length; oneL++) {
                    if (oneLatitude[oneL] == data.result.list[i][one]) {
                        flag = true;
                        break;
                    }
                }
                if (!flag) {
                    oneLatitude.push(data.result.list[i][one]);
                }
            }
        }
        for (var indexLiIndex = 0; indexLiIndex < data.indexLi.length; indexLiIndex++) {
            for (var k = 0; k < secondLatitude.length; k++) {
                var arr = [];
                var secondValue = secondLatitude[k];
                var secondRealValue = secondLatitudeRealName[k];
                if (second != 'REGION_') {
                    secondRealValue = secondValue;
                }
                showData = {
                    title: data.indexLi[indexLiIndex].columnNameZh,
                    labelId: secondValue,
                    label: secondRealValue + data.indexLi[indexLiIndex].columnNameZh,
                    color: formatData.colors[k % 10],
                    workDescribe: data.workDescribe,
                    data: []
                }

                //特殊处理
                for (var i = 0; i < oneLatitude.length; i++) {
                    var oneValue = oneLatitude[i];
                    for (var j = 0; j < data.result.list.length; j++) {
                        if (data.result.list[j][data.latitudeLi[0].columnNameEn] == oneValue) {
                            if (data.result.list[j][data.latitudeLi[1].columnNameEn] == secondValue) {
                                arr = [];
                                oneValue = formatData.timeChangeData(data, oneValue);
                                arr.push(oneValue);
                                arr.push(data.result.list[j][data.indexLi[indexLiIndex].columnNameEn]);
                                showData.data.push(arr);
                                break;
                            }
                        }
                    }
                }
                arrs.push(showData);
            }
        }
        return arrs;
    },

    //少于等于两个维度的数据处理
    formatLessThanTwoLatitudeData: function (data) {
        //临时处理
        for (var i = 0; i < data.latitudeLi.length; i++) {
            if (data.latitudeLi[i].columnNameEn == "REGION_NAME") {
                data.latitudeLi.splice(i, 1);
                break;
            }
        }
        //适合一个指标一个维度、一个指标两个维度、多个指标一个维度、两个指标两个维度

        var arr = [];
        if (data.result.list.length > 0) {
            if (data.latitudeLi.length > 0) {
                //var wd = data.latitudeLi[0].columnNameEn;
                if (data.latitudeLi.length > 2) {
                    return arr;
                }
                //一个指标两个维度
                if (data.latitudeLi.length == 2 && data.indexLi.length == 1) {
                    arr = formatData.OneIndexTwoLat(data);
                }
                    //多个指标一个维度
                else if (data.latitudeLi.length == 1 && data.indexLi.length >= 1) {
                    arr = formatData.MultiIndexOneLat(data);
                }
                    //两个指标两个维度
                else if (data.latitudeLi.length == 2 && data.indexLi.length == 2) {
                    arr = formatData.TwoIndexTwoLat(data);
                }
            }
        }
        return arr;
    }
};



