import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import './untils/formatData'
import './untils/iosORandroid'
import routerMap from './router'
import { getAccessToken } from './untils/api'
import "whatwg-fetch"
import promise from "es6-promise"
import { sync } from 'vuex-router-sync'
import store from './untils/store'
import 'assets/css/common.css'

const FastClick = require('fastclick');
FastClick.attach(document.body);

Vue.directive('echarts', require('./components/modules/directives/echart'));
Vue.use(VueRouter);

global.commit = store.commit || store.dispatch;

const router = new VueRouter({
  hashbang: true,
  history: false,
  saveScrollPosition: true,
  suppressTransitionError: true
});

routerMap(router);

router.start(App, '#app')



